.imageupload {
    display: flex;
    flex-direction: column;
    width: 60%;
    margin: 50px auto;
    position: fixed;
    bottom: 0%;
    left: 50%;
    transform: translateX(-50%);
    background: #ddd;
    padding: 20px;
    box-sizing: border-box;
    z-index: 1;
}

.imageupload > * {
    margin: 5px 0;
}

.imageupload__progress {
    width: 100%;
}