* {
    margin: 0;
}

.app {
    background: #fafafa;
    height: 100vh;
}

.app__header {
    background: white;
    position: sticky;
    top: 0;
    padding: 20px;
    border-radius: 1px solid lightgrey;
    object-fit: contain;
    display: flex;
    justify-content: space-between;
    z-index: 1;
}

.app__account{
    display: flex;
}

.app_headerImage {
    object-fit: contain;
}

.app__signup {
    display: flex;
    flex-direction: column;
}

.app__signup > * {
    margin: 10px 0;
}

.app__post {
    padding: 20px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 50px;
    width: 65%;
    margin: 0 auto;

}

.app__loginToUpload {
    position: absolute;
    bottom: 0%;
    left: 50%;
    transform: translateX(-50%);
}
