.post {
    background: white;
    max-width: 450px;
    border: 1px solid lightgray;
    margin-bottom: 50px;
}

.post__header {
    display: flex;
    align-items: center;
    padding: 10px;
}

.post__header h3 {
    margin-left: 10px;
}

.post__image {
    width: 100%;
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
}

.post h4 {
    font-weight: normal;
    padding: 10px;
}

.post__commentBox {
    display: flex;
    position: relative;
    left: 50%;
    transform: translateX(-45%);
    margin: 18px 0;
}

.comment__input {
    border: 1px solid #ddd;
    outline: 1px solid #ddd;
    width: 75%;
}

.comment__button {
    margin-left: 5px;
    background: #0095f6;
    border: none;
    color: white;
    padding: 0 10px;
    border-radius: 3px;
    cursor: pointer;
}

.post__comments {
    margin-left: 10px;
}